@font-face {
  font-family: "Rubik";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/RubikMedium/Rubik-Medium.eot");
  src: url("../fonts/RubikMedium/Rubik-Medium.eot?#iefix") format("embedded-opentype"), url("../fonts/RubikMedium/Rubik-Medium.woff") format("woff"), url("../fonts/RubikMedium/Rubik-Medium.ttf") format("truetype");
}

@font-face {
  font-family: "Rubik";
  font-weight: 300;
  font-style: normal;
  src: url("../fonts/RubikRegular/Rubik-Regular.eot");
  src: url("../fonts/RubikRegular/Rubik-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/RubikRegular/Rubik-Regular.woff") format("woff"), url("../fonts/RubikRegular/Rubik-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "Rubik";
  font-weight: 100;
  font-style: normal;
  src: url("../fonts/RubikLight/Rubik-Light.eot");
  src: url("../fonts/RubikLight/Rubik-Light.eot?#iefix") format("embedded-opentype"), url("../fonts/RubikLight/Rubik-Light.woff") format("woff"), url("../fonts/RubikLight/Rubik-Light.ttf") format("truetype");
}

ul,
li {
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

@keyframes componentsToHeroTranslateX {
  from {
    transform: translateX(-100%);
  }

  to {
    transform: translateX(0%);
  }
}

@keyframes componentsToHeroOpacity {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes down {
  0% {
    transform: translateY(-30px);
  }

  30%, 70% {
    transform: translateY(0px);
  }

  100% {
    transform: translateY(30px);
  }
}

html,
body {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  background-color: #0e1f29;
}

.container {
  z-index: 5;
  padding: 40px 80px 0px 60px;
  width: 100%;
}

.gradietn {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(150deg, #0b1d26, transparent);
  z-index: 4;
}

.gradietn_footer {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 15%;
  background: rgb(14, 31, 41);
  background: linear-gradient(180deg, rgba(14, 31, 41, 0) 0%, rgba(14, 31, 41, 0.8239670868) 73%, rgb(14, 31, 41) 100%);
  z-index: 5;
}

.header_nav {
  display: flex;
  justify-content: space-between;
  text-align: center;
  align-items: center;
  max-height: 25px;
  width: 100%;
  font-family: "Roboto", serif;
  font-style: normal;
  z-index: 2;
}

.header_nav_title {
  color: #fff;
  font-family: "Playfair Display", serif;
  font-weight: 700;
  font-size: 32px;
  text-transform: uppercase;
}

.header_nav_title:hover {
  color: #FBD784;
}

.header_nav_links {
  color: #fff;
}

.header_nav_links_ul {
  display: flex;
  list-style: none;
  font-weight: 700;
  font-size: 18px;
}

.header_nav_links_li:not(:last-child) {
  margin-right: 40px;
}

.header_nav_links a {
  color: #fff;
  text-decoration: none;
  transition: 0.35s;
}

.header_nav_links a:hover {
  color: #FBD784;
}

.header_nav_acc_logo {
  margin-right: 5px;
}

.header_nav_acc_wrapp {
  display: flex;
  align-items: center;
  font-size: 17px;
  font-weight: 700;
  color: #fff;
}

.header_nav_acc_wrapp:hover {
  color: #FBD784;
  fill: #FBD784;
}

.header_nav_acc a {
  text-decoration: none;
}

.layers {
  height: 100vh;
  display: flex;
  position: relative;
  overflow: hidden;
}

.layer {
  position: absolute;
  height: 100%;
  width: 100%;
  background-size: cover;
}

.layer_top {
  z-index: 1;
  transform: translate3d(0, calc(var(--scrollTop) * -0.5), 0);
  height: 150vh;
}

.layer_middle {
  top: 40%;
  z-index: 2;
  transform: translate3d(0, calc(var(--scrollTop) * 0.3), 0);
}

.layer_bottom {
  top: 60%;
  z-index: 3;
  transform: translate3d(0, calc(var(--scrollTop) / -5.5), 0);
}

.hero {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 200px;
  transition: transform 1s ease-in-out;
}

.hero_social_ul {
  list-style: none;
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  line-height: 0px;
  color: #fff;
  transform: rotate(90deg);
  margin-left: -75px;
  display: flex;
  align-items: center;
}

.hero_social_li:not(:last-child) {
  margin-right: 25px;
}

.hero_social_svg {
  transform: rotate(-90deg);
  width: 24px;
  height: 24px;
  transition: 0.35s;
}

.hero_social_svg_inst {
  scale: 1.5;
}

.hero_social_svg_twit:hover {
  scale: 1.5;
}

.hero_social_svg_inst:hover {
  scale: 2;
}

.hero_social:nth-child(1) {
  animation: componentsToHeroTranslateX 0.4s ease-in-out;
}

.hero_title_span {
  display: flex;
  align-items: center;
  font-family: "Roboto", sans-serif;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 6px;
  text-transform: uppercase;
  color: #FBD784;
}

.hero_title_span::selection {
  color: #000;
  background-color: transparent;
}

.hero_title_span_animation {
  animation: componentsToHeroTranslateX 0.9s ease-in-out;
}

.hero_title_span::before {
  content: "";
  display: block;
  border-bottom: 2px solid #FBD784;
  width: 72px;
  color: #FBD784;
  margin-right: 32px;
}

.hero_title_h1 {
  max-width: 950px;
  font-family: "Playfair Display", serif;
  font-weight: 400;
  font-size: 88px;
  color: #fff;
  animation: componentsToHeroOpacity 1.6s ease-in-out;
}

.hero_title_h1::selection {
  color: #FBD784;
  background-color: transparent;
}

.hero_title_scroll {
  display: flex;
  font-family: "Roboto", sans-serif;
  font-size: 18px;
  font-weight: 500;
  color: #fff;
  margin-top: 20px;
  overflow: hidden;
}

.hero_title_scroll span::selection {
  color: #FBD784;
  background-color: transparent;
}

.hero_title_scroll_arrow {
  margin-left: 15px;
  animation-name: down;
  animation-duration: 1.5s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.hero_nav {
  display: flex;
  min-width: 85px;
  flex-direction: column;
  align-items: center;
  font-family: "Roboto", sans-serif;
  font-size: 18px;
  font-weight: 700;
  color: #fff;
  position: relative;
}

.hero_nav::after {
  content: "";
  position: absolute;
  top: -10%;
  right: 0;
  height: 120%;
  width: 1px;
  background-color: rgba(255, 255, 255, 0.35);
}

.hero_nav_ul {
  list-style: none;
  text-align: center;
}

.hero_nav_li {
  position: relative;
}

.hero_nav_scroll_link {
  color: #fff;
  text-decoration: none;
  transition: 0.3s;
}

.hero_nav_scroll_link:hover {
  color: #FBD784;
}

.hero_nav ul > li:not(:last-child) {
  margin-bottom: 40px;
}

.hero_nav_span {
  height: 62px;
  width: 1px;
  background-color: #fff;
  opacity: 0;
  position: absolute;
  right: -23px;
  top: -21px;
  transition: all 2s;
}

.hero_nav_span_active {
  opacity: 1;
}

.main {
  display: flex;
  flex-direction: column;
  background-color: #0e1f29;
}

.main_block {
  display: flex;
  justify-content: space-around;
  align-items: center;
  max-width: 100%;
  height: 100vh;
  padding: 0 230px;
  position: relative;
  margin-bottom: 200px;
}

.main_block_span {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 240px;
  opacity: 0.15;
  color: #fff;
  line-height: 240px;
  position: absolute;
  top: -27%;
  left: -21%;
  transform: translateX(-200px);
  scale: 0;
  opacity: 0;
}

.main_block_span.show_animation {
  transition: all 0.7s;
  transform: translateX(0);
  scale: 1;
  opacity: 0.15;
}

.main_block_span.selectDisabled {
  user-select: none !important;
}

.main_block_span2 {
  scale: 0;
  opacity: 0;
  transform: translateY(40px);
  transition: all 0.7s;
}

.main_block_span2.show_animation {
  transition-delay: 0.4s;
  transform: translateY(0px);
  scale: 1;
  opacity: 1;
}

.main_block_descr {
  display: flex;
  justify-content: center;
  align-self: center;
  flex-direction: column;
  position: relative;
}

.main_block_descr::selection {
  color: #FBD784;
  background-color: transparent;
}

.main_block_descr_title {
  font-family: "Playfair Display", serif;
  font-size: 64px;
  font-weight: 600;
  color: #fff;
  max-width: 555px;
  opacity: 0;
  transform: translateY(250px);
  transition: all 0.7s;
}

.main_block_descr_title.show_animation {
  transition-delay: 0.8s;
  opacity: 1;
  transform: translateY(0);
}

.main_block_descr_title::selection {
  color: #FBD784;
  background-color: transparent;
}

.main_block_descr_p {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 32px;
  color: #fff;
  max-width: 630px;
  opacity: 0;
  transform: translateY(250px);
  transition: all 0.7s;
}

.main_block_descr_p.show_animation {
  transition-delay: 1.2s;
  opacity: 1;
  transform: translateY(0);
}

.main_block_descr_p::selection {
  color: #FBD784;
  background-color: transparent;
}

.main_block_descr_link {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  text-decoration: none;
  color: #FBD784;
  scale: 0;
  transition: scale 0.7s ease-out 1.6s, color 0.2s ease-out 0s;
}

.main_block_descr_link.show_animation {
  scale: 1;
}

.main_block_descr_link:hover {
  color: rgb(99, 83, 83);
}

.main_block_descr_img {
  height: 720px;
  transform: translateY(30px) rotate(15deg);
  opacity: 0;
  transition: all 2s;
}

.main_block_descr_img.show_animation {
  transition-delay: 1.99s;
  transform: translateY(0) rotate(0);
  opacity: 1;
}

.main_even {
  flex-direction: row-reverse;
}

.footer {
  display: flex;
  justify-content: space-between;
  padding: 0 230px;
  background-color: #0e1f29;
  margin-bottom: 120px;
}

.footer_left {
  color: #fff;
  max-width: 300px;
}

.footer_left_span {
  font-family: "Playfair Display", serif;
  font-weight: 500;
  font-size: 32px;
}

.footer_left_span::selection {
  color: #FBD784;
  background-color: transparent;
}

.footer_left_p {
  font-family: "Roboto", sans-serif;
  max-width: 280px;
  font-weight: 600;
  font-size: 18px;
}

.footer_left_p::selection {
  color: #FBD784;
  background-color: transparent;
}

.footer_right {
  display: flex;
  column-gap: 100px;
  font-family: "Roboto", sans-serif;
}

.footer_right_title {
  font-size: 24px;
  font-weight: 700;
  color: #FBD784;
}

.footer_right_title::selection {
  color: #000;
  background-color: transparent;
}

.footer_right_links li {
  list-style: none;
}

.footer_right_links li:not(:last-child) {
  margin-bottom: 15px;
}

.footer_right_links li > a {
  text-decoration: none;
  color: #fff;
}

.footer_right_links li a {
  position: relative;
}

.footer_right_links li a::after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: #FBD784;
  position: absolute;
  bottom: -20%;
  left: 0;
  transform: scaleX(0);
  transition: transform 0.2s ease-in-out;
}

.footer_right_links li a:hover:after {
  transform: scaleX(1);
}

.footer_right_list_wrapp {
  display: flex;
  flex-direction: column;
  gap: 15px;
}